































export default {
	data() {
		return {
			region: [],
			customItem: '全部',
			id: '',
			address: {}
		};
	},
	onLoad(options) {
		console.log(options.id)
		this.id = options.id
		this.getAddressItem()
	},
	methods: {
		bindPickerChange(e) {
			this.region = e.detail.value
		},
		getAddressItem() {
			var that = this
			this.$api.get('/api/address/getAddressItem', {
				address_id: this.id
			}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					var data = res.data.data
					that.address = data
					that.region[0] = data.province_text
					that.region[1] = data.city_text
					that.region[2] = data.area_text
				}
			})
		},

		formSubmit: function(e) {
			console.log(e)
			var that = this
			var formData = e.detail.value
			var phoneReg = /^1[3-578]\d{9}$/
			if (!phoneReg.test(formData.phone)) {
				return uni.showToast({
					title: '请输入正确的手机号',
					icon: 'none'
				})
			}
			var para = {
				id: this.id,
				name: formData.name,
				phone: formData.phone,
				province: this.region[0],
				city: this.region[1],
				area: this.region[2],
				address: formData.address,
				is_default: formData.isDefault ? 1 : 0
			}
			this.$api.post('/api/address/addOrEditAddress', para, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					uni.showToast({
						title: "修改成功!"
					})
					setTimeout(function() {
						uni.navigateBack()
					}, 1000)
				}
			})
		},
		delAdd() {
			var that = this
			uni.showModal({
				title: '确认删除该地址吗?',
				confirmColor: '#e9101f',
				success: function(res) {
					if (res.confirm) {
						that.$api.get('/api/address/delAddress', {
							id: that.id
						}, function(res) {
							console.log(res)
							if (res.data.code == 200) {
								uni.showToast({
									title: "删除成功!"
								})
								setTimeout(function() {
									uni.navigateBack()
								}, 1000)
							}
						})
					}
				}
			})

		}
	}
}
